<template>
  <ValidationProvider
    :rules="validationRules"
    :name="feedbackId"
    v-slot="vContext"
  >
    <div class="counter-input-contianer">
      <button
        type="button"
        @click="decrement"
        :class="{ 'counter-disabled': counterValue == min }"
      >
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M18 13H6C5.45 13 5 12.55 5 12C5 11.45 5.45 11 6 11H18C18.55 11 19 11.45 19 12C19 12.55 18.55 13 18 13Z"
            fill="#004ED4"
          />
        </svg>
      </button>
      <b-form-input
        @input="$emit('inputt', counterValue)"
        type="number"
        class="counter-value"
        v-model="counterValue"
        @change="onInputChange"
        :aria-describedby="feedbackId"
        :min="min"
        :max="max"
      ></b-form-input>

      <button
        type="button"
        @click="increment"
        :class="{ 'counter-disabled': counterValue == max }"
      >
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M18 13H13V18C13 18.55 12.55 19 12 19C11.45 19 11 18.55 11 18V13H6C5.45 13 5 12.55 5 12C5 11.45 5.45 11 6 11H11V6C11 5.45 11.45 5 12 5C12.55 5 13 5.45 13 6V11H18C18.55 11 19 11.45 19 12C19 12.55 18.55 13 18 13Z"
            fill="#004ED4"
          />
        </svg>
      </button>
    </div>
    <transition name="slide">
      <b-form-invalid-feedback :id="feedbackId" class="error-field">{{
        !vContext.valid && vContext.errors[0]
      }}</b-form-invalid-feedback>
    </transition>
  </ValidationProvider>
</template>

<script>
// TODO : this component has to be replaced with the bootstrap spin button
export default {
  name: 'CustomCounter',
  inheritAttrs: false,
  props: {
    value: {
      // this prop for v-model
      required: true,
      type: Number,
    },
    min: {
      required: false,
      type: Number,
      default: 0,
    },
    max: {
      required: false,
      type: Number,
      default: 5000,
    },
    validationRules: {
      type: String,
      default: '',
    },
    step: {
      required: false,
      default: 1,
    },
    validationName: {
      type: String,
      required: false,
      default: 'text',
    },
  },
  data() {
    return {
      counterValue: 0,
    }
  },
  computed: {
    feedbackId() {
      return 'input' + this.validationName.replace(/ /g, '-') + '-feedback'
    },
  },
  methods: {
    increment() {
      if (parseInt(this.counterValue) < this.max){
        this.emitInputEvent(parseInt(this.counterValue) + this.step)
        this.updateCounterValue(parseInt(this.counterValue) + this.step);
        this.$emit('changee')
      }
    },
    decrement() {
      if (parseInt(this.counterValue) > this.min) {
        this.emitInputEvent(parseInt(this.counterValue) - this.step)
        this.updateCounterValue(parseInt(this.counterValue) - this.step);
        this.$emit('changee')
      }
    },
    emitInputEvent(value) {
      this.$emit('input', value)
    },
    validateNumber(newValue) {
      return newValue >= this.min && newValue <= this.max
    },
    getValidationState(vContext) {
      return vContext.errors[0] ? false : vContext.valid ? true : null
    },
    onInputChange() {
      this.validateNumber(this.counterValue)
        ? this.emitInputEvent(this.counterValue)
        : this.counterValue > this.max ? this.updateCounterValue(this.max) : this.updateCounterValue(this.min)
    },
    updateCounterValue(value) {
      this.counterValue = value
    },
  },
  mounted() {
    if(this.value){
      this.updateCounterValue(this.value)
    }else{
      this.updateCounterValue(this.min)
    }

  },
  watch: {
    value(val) {
      this.updateCounterValue(val)
    },
  },
}
</script>

<style scoped>
.counter-input-contianer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 8px;
  padding: 12px;
}
button {
  background: none;
  border: solid 2px rgb(72, 72, 255);
  border-radius: 100%;
  width: 27px;
  height: 27px;
  padding: 2px;
  cursor: pointer;
}
button.counter-disabled {
  cursor: default;
  opacity: 0.4;
}
.counter-value {
  width: auto;
  min-width: 50px;
  height: auto;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  border: none;
  padding: 0;
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
  background-color: #e9ecef;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>
